import * as Yup from 'yup';
import {  useEffect, useState, } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
// swal
import swal from 'sweetalert';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Stack, Alert, Typography } from '@mui/material';
import axios from '../../../../utils/axios';
import { FormProvider, RHFSelect,  RHFTextField, RHFSwitch } from '../../../../components/hook-form';
// format number 
import { fCurrency,} from '../../../../utils/formatNumber';
import useAuth from '../../../../hooks/useAuth';
import Iconify from '../../../../components/Iconify';

import useIsMountedRef from '../../../../hooks/useIsMountedRef';
import { PATH_DASHBOARD } from '../../../../routes/paths';
// ----------------------------------------------------------------------

export default function Purchase2Airtime() {

  const NewUserSchema = Yup.object().shape({
    phone: Yup.string().required('Phone Number Required'),
    type: Yup.string().required('Payment Type Required')
  });
const pinSchema =  Yup.object().shape({
  phone: Yup.string().required('Phone Number Required'),
  pin: Yup.string().min(4, 'Transaction Pin Must Be 4 Digits').max(4, 'Transaction Pin Must Be 4 Digit').required('Transaction Pin Required'),
  type: Yup.string().required('Payment Type Required')
});
const { enqueueSnackbar } = useSnackbar();
const navigate = useNavigate();
  const [Habukhannetwork, setNetwork] = useState();
  const [NumberValidator, SetValidate] = useState('');
  const [amount, SetAmount] = useState();
  const [number, SystemNumber] = useState();
  const [loading, Setloading] = useState(false);
  const AccessToken =  window.localStorage.getItem('accessToken');
  const {setting} = useAuth();
  const isMountedRef = useIsMountedRef();
  const pin = setting?.setting?.allow_pin === 1;
  const methods = useForm({
    resolver: yupResolver(pin? pinSchema  :NewUserSchema),
  });
  const [discount, SetDiscount] = useState();
  const { 
    setValue,
    watch,
    setError,
    handleSubmit,
  
    formState: {errors},
  } = methods;
  const values = watch();
 const planType = [
    {value: 'Bank', code: 'bank'},
    {value: 'Wallet Funding', code: 'wallet'}
 ]
 // get network over here
 useEffect(() => {
  axios.get('/api/website/app/network').then(data => {
     setNetwork(data.data?.network);
  });
  axios.get('/api/airtimecash/number').then(data => SystemNumber(data.data?.numbers))
}, []);

  const onSubmit = async (data) => {
    try {
        let direct = '';
        let title = '';
        // swal here
        if(data.network === 'MTN'){
         title = 'How to Transfer on MTN'; 
           
         direct = `Before you send an airtime, you Must have a 4 digit pin.\n Press: *600 *000*Newpin*Newpin # \n Press: *600*${number?.mtn_number}*${amount}*PIN# \n Note you are to send ₦${amount} ${data?.network} airtime`;    
        }else if(data.network === 'GLO'){
            title = 'How to Transfer on Glo';
            direct = `Before you send an airtime, you Must have a 5 digit pin \n Press: *132 *00000*Newpin *Newpin # \n Press: *131*${number?.glo_number}*${amount}*PIN# \n Note you are to send ₦${amount} ${data?.network} airtime \n How to transfer Airtime on GLO:  *131*${number?.glo_number}*${amount}*PIN# \n Kindly Transfer the sum of ${amount} to ${number?.glo_number} ,follow the process above`
        }else if(data.network === 'AIRTEL'){
            title = 'How to Transfer on Airtel';
            direct = `Before you send an airtime, you Must have a 4 digit pin \n Press: *432# \n Select 'Pin Management' @ number4 option. \n  Press 1 to change pin. \n Your current Pin should be your default pin -- 0000 \n Press: *432 * 1  \n Input the phone number you want to transfer to  (${number?.airtel_number}) \n  Input the amount involve ₦${amount}. \n Note you are to send ₦${amount} ${data?.network} airtime \n How to transfer Airtime on AIRTEL:*432*1*#  To ${number?.airtel_number} \n Kindly Transfer the sum of  ₦${amount} to ${number?.airtel_number} ,follow the process above`
        }else{
            title = 'How to Transfer on 9MOBILE';
            direct = `Before you send an airtime, you Must have a 4 digit pin \n Press *247*0000*newpin# \n Press: *131*PIN*${amount}*${number?.mobile_number} \n  Kindly Transfer the sum of  ₦${amount} to ${number?.airtel_number} ,follow the process above`
        }
       
        swal({
            title: `${title}`,
            text: `${direct}`,
            icon: "info",
            button: {
                text: "Proceed",
                closeModal: false,
              },
          }).then(() => {
            Setloading(true);
            axios.post('/api/cash',{
             network: data.network,
             payment_type: data.type,
             sender_number: data.phone,
             amount,
             token: AccessToken,
             pin: data.pin,
             bypass: data?.bypass || false 
            }).then((resp) => {
                Setloading(false)
               swal({ title: 'success', text: `Transaction On process`, icon: 'success', buttons: [
          'Okay',
          'Invoice'
        ],
        dangerMode: true,}).then((isConfirm) => {
        if (isConfirm) {
          navigate(`${PATH_DASHBOARD.general.invioce}/${resp?.data?.transid}/airtimecash`, { replace: true });
        }
      });
            }).catch((error) => {
                if(error?.data?.message !== undefined){
                    swal('Opps', `${error?.data?.message}`, 'error');
                }else if(error?.message !== undefined){
                    swal('Opps', `${error?.message}`, 'error');
                }else{
                    swal('Opps', 'Something went wrong', 'error');
                }
               
                Setloading(false);
            });
          })
       
    } catch (error) {
      if (isMountedRef.current) {
        setError('afterSubmit', error);
      }
    }
  };
  let theNetwork;
  let verNetwork;
  let ic;
  const checkphone = (phone) => {
   const phoneNumber = phone.target.value;
   setValue('phone',phoneNumber)
   if(values.network !== null && values.network !== undefined &&  values.network !== ''){
   
    if(values.network  === '1'){
          theNetwork = 'MTN';
    }else if(values.network === '2'){
       theNetwork = 'AIRTEL';
    }else if(values.network === '3'){
       theNetwork = 'GLO';
    }else if(values.network === '4'){
       theNetwork = '9MOBILE';
    }else{
      theNetwork = values.network;
    }
    if(phoneNumber ==="" || phoneNumber.length< 6 ){
      SetValidate('')
    }else{
      SetValidate('')
      if(/0702|0704|0803|0806|0703|0706|0813|0816|0810|0814|0903|0906|0913/.test(phoneNumber)){
        verNetwork="MTN";
      }else if(/0805|0807|0705|0815|0811|0905/.test(phoneNumber))
      {
        verNetwork="GLO";
      }else if(/0702|0704|0803|0806|0703|0706|0813|0816|0810|0814|0903|0906|0913/.test(phoneNumber))
      {
        verNetwork="GIFTING";
      }
      else if(/0802|0808|0708|0812|0701|0901|0902|0907|0912/.test(phoneNumber))
      {
        verNetwork="AIRTEL";
      }
      else if(/0809|0818|0817|0908|0909/.test(phoneNumber))
      {
        verNetwork="9MOBILE";
      }
      else if(/0804/.test(phoneNumber))
      {
        verNetwork="NTEL";
      }
      else
      {
        verNetwork="Unable to identify network !";
      }
      if (verNetwork === theNetwork)
		{
			 ic = <Iconify icon="icon-park:success"  sx={{ width: 20, height: 20,color: 'success.main'}} />
		}
		else 
		{
			ic = <Iconify icon="akar-icons:circle-alert"  sx={{ width: 20, height: 20,color: 'error.main'}} />
		}
    
    SetValidate(  <>
    <Typography variant="subtitle2" sx={{ mb: 0.5 }}> Network is: <b>{verNetwork} </b>{ic}<br /><i><b> NB: </b> Ignore this warning for <b>Ported Numbers</b></i></ Typography> </>);
    }
    
   
   }else{
    enqueueSnackbar('Network is Required', { variant: 'error' })   
   }
  }

  const  Calculate = async (data) => {
      SetAmount(data.target.value);
      axios.post(`/api/airtimecash/discount/admin`,{
        amount: data.target.value,
        network: values.network
      }).then(data => SetDiscount(`₦${fCurrency(data?.data?.amount)}`)).catch((error) => {
      SetDiscount(undefined); 
      if(error.data?.message !== undefined){
        enqueueSnackbar(error.data?.message, { variant: 'error' })  
         } else if(error?.message !== undefined){
            enqueueSnackbar(error?.message, { variant: 'error' })   
         }else{
            enqueueSnackbar('Something Went Wrong', { variant: 'error' })   
         }
      })
  }
 

  
  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
      <Grid item xs={12} md={8}>
      {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: 'grid',
                columnGap: 1,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
              }}
            >
              <RHFSelect name="network" label="Network" placeholder="Network">
                <option value="" />
                {Habukhannetwork?.map((option) => (
                  <option key={option?.code} value={option?.network} disabled = {option?.cash === 0}>
                    {option?.network}
                  </option>
                ))}
              </RHFSelect>

              <RHFSelect name="type" label="Payment Method">
                <option value="" />
                {planType?.map((option) => (
                  <option key={option?.code} value={option?.code}>
                    {option?.value}
                  </option>
                ))}
              </RHFSelect>
              <RHFTextField name="phone" label="Sender Phone Number" type="number" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}  onChange={checkphone} />
            <>{NumberValidator}</>
              <RHFTextField name="amount" label="Amount" type="number" value={amount} inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} onChange={Calculate}/>
             {pin && <RHFTextField name="pin" label="Transaction Pin" type="password" autoComplete="new-password" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>}
           
             {discount !== undefined && <RHFTextField name="discount" label="Amount To Be Credited" value={discount} disabled/>}
             <RHFSwitch
                name='bypass'
                labelPlacement="start"
                def={false}
                label={
                  <>
                    <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                     Bypass Phone Number
                    </Typography>
                  </>
                }
                sx={{ mx: 0, width: 1, justifyContent: 'space-between' }}
              />
            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton  fullWidth size="large" type="submit" variant="contained" loading={loading}>
               Convert
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
